import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useMutation, useQuery } from "@apollo/react-hooks";
import { gql } from "apollo-boost";

import {
  Container,
  TextField,
  Typography,
  FormControlLabel,
  Button,
  Radio,
  RadioGroup,
  FormControl,
  Select,
  InputLabel,
} from "@material-ui/core";

const ADDUSER = gql`
  mutation addWorker(
    $CompaniesId: ID!
    $role: String
    $name: String
    $username: String
    $password: String
  ) {
    register(
      name: $name
      username: $username
      password: $password
      role: $role
      CompaniesId: $CompaniesId
    ) {
      name
    }
  }
`;

const GETCOMPANIES = gql`
  {
    getCompany {
      id
      name
    }
  }
`;

const useStyles = makeStyles((theme) => ({
  formControl: {
    marginTop: theme.spacing(1),
  },
  paper: {
    marginTop: theme.spacing(8),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  form: {
    width: "100%",
    marginTop: theme.spacing(1),
  },
  radiogroup: {
    justifyContent: "space-around",
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
}));

function Add({ refetch }) {
  const classes = useStyles();
  const [name, setName] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [permission, setPermission] = useState("");
  const [company, setCompany] = useState("");
  const { data } = useQuery(GETCOMPANIES);
  const [register] = useMutation(ADDUSER);

  const onclick = async (e) => {
    e.preventDefault();
    await register({
      variables: {
        name: name,
        username: username,
        password: password,
        role: permission,
        CompaniesId: company,
      },
    });
    refetch();
  };

  return (
    <Container component="main" maxWidth="xs">
      <div className={classes.paper}>
        <Typography component="h1" variant="h5">
          Cadastrar usuário
        </Typography>
        <form className={classes.form} noValidate>
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="name"
            label="Nome"
            name="name"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="username"
            label="Username"
            name="username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
          <FormControl
            required
            fullWidth
            variant="outlined"
            className={classes.formControl}
          >
            <InputLabel>Empresa</InputLabel>
            <Select
              native
              label="Empresa"
              onChange={(e) => setCompany(e.target.value)}
            >
              <option aria-label="None" value="" />
              {data &&
                data.getCompany.map((item) => (
                  <option value={item.id}>{item.name}</option>
                ))}
            </Select>
          </FormControl>
          <Typography align="center"> Permissões </Typography>
          <RadioGroup
            defaultValue="male"
            aria-label="gender"
            name="customized-radios"
            onChange={(e) => setPermission(e.target.value)}
            row
            className={classes.radiogroup}
          >
            <FormControlLabel
              value="admin"
              control={<Radio color="primary" />}
              label="admin"
            />
            <FormControlLabel
              value="subadmin"
              control={<Radio color="primary" />}
              label="subadmin"
            />
          </RadioGroup>
          <Button
            onClick={onclick}
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
          >
            Cadastrar
          </Button>
        </form>
      </div>
    </Container>
  );
}

export default Add;
