import React, { useState, useEffect } from "react";
import styled from "styled-components";

function List({
  data = [],
  provider = { state: { add: null, update: null }, dispatch: () => {} },
}) {
  const [items, setItems] = useState(data);
  const { state, dispatch } = provider;

  useEffect(() => {
    setItems(data);
  }, [data]);
  return (
    <>
      <Title>
        Usuários
        {state.add || state.update ? (
          <Close
            onClick={() => {
              dispatch((state) => ({
                ...state,
                add: false,
                update: false,
                option: false,
              }));
            }}
          >
            +
          </Close>
        ) : (
          <Add
            onClick={() => {
              dispatch((state) => ({ ...state, add: true, option: true }));
            }}
          >
            +
          </Add>
        )}
      </Title>

      <Wrapper>
        <Header>
          <Item>ID</Item>
          <Item>Nome</Item>
          <Item>Empresa</Item>
          <Item>Username</Item>
          <Item>Permissão</Item>
        </Header>

        {items.map((item) => (
          <Items key={item.id}>
            <Item>{item.id}</Item>
            <Item>{item.name}</Item>
            <Item>{item.Company && item.Company.name}</Item>
            <Item>{item.username}</Item>
            <Item>{item.role}</Item>
          </Items>
        ))}
      </Wrapper>
    </>
  );
}

const Wrapper = styled.div`
  width: 95%;
`;

const Title = styled.h1`
  color: rgb(50, 50, 50);
  display: flex;
  align-items: center;
`;

const Add = styled.span`
  font-size: 50px;
  color: rgb(90, 150, 90);
  cursor: pointer;
  margin-left: 8px;
`;

const Close = styled(Add)`
  color: rgb(150, 90, 90);
  transform: rotate(45deg);
`;

const Content = styled.ul`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
`;

const Header = styled(Content)`
  font-weight: bold;
`;

const Items = styled(Content)`
  border-radius: 15px;
  transition: all 0.3s;

  :hover {
    border: 0;
    box-shadow: 0 0 3px rgb(50, 50, 50);
  }
`;

const Item = styled.li`
  list-style: none;
  text-align: center;
  margin: 8px 0;
  align-self: center;
  width: 15%;
`;

export default List;
