import React from "react";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";

function ccyFormat(num) {
  return `${num.toFixed(2)}`;
}

function priceRow(qty, unit) {
  return qty * unit;
}

function subtotal(items) {
  let total = 0;
  items.forEach((item) => {
    total = total + Number(priceRow(item.value, item.amount));
  });

  return total;
}

export default class ComponentToPrint extends React.Component {
  render() {
    const {
      Companies,
      Operations,
      Clients,
      discount,
      additional,
      type,
      total,
    } = this.props.note;

    return (
      <TableContainer style={{ padding: 50 }} component={Paper}>
        <Table size="small" aria-label="spanning table">
          <TableHead>
            <TableRow>
              <TableCell>Empresa</TableCell>
              <TableCell>Operação</TableCell>
              <TableCell colSpan={2} align="center">
                {type === "1" ? "Vendedor" : "Comprador"}
              </TableCell>
            </TableRow>
            <TableBody>
              <TableCell>{Companies.name}</TableCell>
              <TableCell>{type === "1" ? "Compra" : "Venda"}</TableCell>
              <TableCell colSpan={2} align="center">
                {Clients.name}
              </TableCell>
            </TableBody>
          </TableHead>
          <TableHead>
            <TableRow>
              <TableCell align="center" colSpan={3}>
                Detalhes
              </TableCell>

              <TableCell align="right">Preço</TableCell>
            </TableRow>
            <TableRow>
              <TableCell>Produto</TableCell>
              <TableCell align="right">Quantidade</TableCell>
              <TableCell align="right">Valor</TableCell>
              <TableCell align="right">Total</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {Operations.map((item) => (
              <TableRow key={item.id}>
                <TableCell>{item.Products.name}</TableCell>
                <TableCell align="right">{item.amount}</TableCell>
                <TableCell align="right">{ccyFormat(item.value)}</TableCell>
                <TableCell align="right">
                  R${ccyFormat(priceRow(item.value, item.amount))}
                </TableCell>
              </TableRow>
            ))}
            <TableRow>
              <TableCell />
              <TableCell colSpan={2}>Subtotal</TableCell>
              <TableCell align="right">
                {ccyFormat(subtotal(Operations))}
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell rowSpan={3} />
              <TableCell colSpan={2}>Adiciontal</TableCell>
              <TableCell align="right">{ccyFormat(additional)}</TableCell>
            </TableRow>
            <TableRow>
              <TableCell colSpan={2}>Desconto</TableCell>
              <TableCell align="right">{ccyFormat(discount)}</TableCell>
            </TableRow>
            <TableRow>
              <TableCell colSpan={2}>Total</TableCell>
              <TableCell align="right">{ccyFormat(total)}</TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    );
  }
}
