import React, { useState, useMemo } from "react";
import styled from "styled-components";
import CompaniesList from "../../../molecules/Companies/List";
import Adder from "../../../molecules/Companies/Add";

import { useQuery } from "@apollo/react-hooks";
import { gql } from "apollo-boost";

const GETCOMPANIES = gql`
  {
    getCompany {
      id
      name
      balance
    }
  }
`;

function DashboardProduct() {
  const [state, dispatch] = useState({
    update: false,
    add: false,
    data: null,
    option: false,
  });

  const provider = useMemo(() => ({ state, dispatch }), [state, dispatch]);

  const { data, loading, refetch } = useQuery(GETCOMPANIES);

  if (loading) {
    return <h1>loading...</h1>;
  }

  return (
    <>
      <List>
        <CompaniesList
          data={data.getCompany}
          provider={provider}
        ></CompaniesList>
      </List>
      {state.option ? (
        <Options>
          {state.add && <Adder refetch={refetch} provider={provider}></Adder>}
        </Options>
      ) : null}
    </>
  );
}

const Wrapper = styled.div`
  padding: 10px 0;
  display: flex;
  align-items: center;
  flex-direction: column;
`;

const List = styled(Wrapper)`
  flex: 2;
`;

const Options = styled(Wrapper)`
  flex: 1.3;
  border-left: 1px dashed rgb(80, 80, 80);

  @media (max-width: 465px) {
    position: absolute;
    background: lightgray;
    width: 100%;
    height: 100%;
    border: 0;
  }
`;

export default DashboardProduct;
