import React, { useState, useRef, useEffect } from "react";
import Modal from "react-modal";
import styled from "styled-components";
import { useMutation, useQuery } from "@apollo/react-hooks";
import { gql } from "apollo-boost";
import { Grid } from "@material-ui/core";

const UPLOAD_FILE = gql`
  mutation singleUpload($file: Upload!, $workerId: ID) {
    singleUpload(file: $file, workerId: $workerId)
  }
`;

const DOWNLOAD_FILE = gql`
  mutation retrieveFile($documentId: ID) {
    retrieveFile(documentId: $documentId) {
      path
      base64
    }
  }
`;

const DELETE_FILE = gql`
  mutation deleteFile($documentId: ID) {
    deleteFile(documentId: $documentId)
  }
`;

const ALL_FILES = gql`
  query uploads($workerId: ID) {
    uploads(workerId: $workerId) {
      id
      path
      WorkersId
    }
  }
`;

Modal.setAppElement("#root");

function ModalTemplate({ open, close, worker }) {
  const refFile = useRef();
  const downbutton = useRef();
  const customStyles = {
    content: {
      boxShadow: "0 0 0 gray",
      borderRadius: "5px",
    },
  };

  const workerId = worker ? worker.id : 0;

  const { data, loading, refetch } = useQuery(ALL_FILES, {
    variables: { workerId: workerId },
  });

  const [fetch] = useMutation(UPLOAD_FILE);
  const [download] = useMutation(DOWNLOAD_FILE);
  const [deletefile] = useMutation(DELETE_FILE);

  const [file, setFile] = useState({});
  const [downloadfile, setDownloadfile] = useState({
    base64: "",
    filename: "",
  });

  async function onChange({
    target: {
      validity,
      files: [file],
    },
  }) {
    if (validity.valid) {
      const myNewFile = new File(
        [file],
        `${worker.id}_${worker.name}_${file.name}`,
        {
          type: file.type,
        }
      );
      setFile(myNewFile);
    }
  }

  async function onSend() {
    await fetch({ variables: { file: file, workerId: worker.id } });
    refFile.current.value = "";
    refetch();
  }

  async function onDownload(id) {
    const {
      data: { retrieveFile },
    } = await download({
      variables: { documentId: id },
    });
    const filename = retrieveFile.path.split("/")[1];
    setDownloadfile({
      base64: retrieveFile.base64,
      filename,
    });
  }

  async function onDeleteFile(id) {
    await deletefile({ variables: { documentId: id } });
    refetch();
  }

  useEffect(() => {
    if (downloadfile.filename !== "") {
      downbutton.current.click();
    }
  }, [downloadfile]);

  return (
    <Modal isOpen={open} onRequestClose={close} style={customStyles}>
      <Grid
        style={{ position: "relative" }}
        container
        direction="column"
        justify="center"
        alignItems="center"
      >
        <Grid container justify="center" alignItems="center">
          <Input
            type="file"
            accept="application/pdf"
            onChange={onChange}
            ref={refFile}
          ></Input>
          <Button onClick={onSend}>enviar</Button>
          <Button leave red onClick={close}>
            X
          </Button>
        </Grid>

        <Grid
          container
          item
          xs={11}
          style={{ border: "1px solid gray", borderRadius: "5px" }}
        >
          {!loading &&
            data &&
            data.uploads &&
            data.uploads.map((item) => (
              <Grid key={item.id} container direction="column">
                <Grid
                  container
                  direction="row"
                  item
                  justify="space-around"
                  alignItems="center"
                >
                  <Grid item xs={6}>
                    <Paragraph>{item.path.split("/")[1]}</Paragraph>
                  </Grid>
                  <Grid item xs={3}>
                    <Button
                      onClick={() => {
                        onDownload(item.id);
                      }}
                    >
                      download
                    </Button>
                  </Grid>
                  <Grid item xs={3}>
                    <Button
                      red
                      onClick={() => {
                        onDeleteFile(item.id);
                      }}
                    >
                      delete
                    </Button>
                  </Grid>

                  <Down
                    ref={downbutton}
                    href={`data:application/pdf;base64,${downloadfile.base64}`}
                    download={downloadfile.filename}
                  ></Down>
                </Grid>
              </Grid>
            ))}
        </Grid>
      </Grid>
    </Modal>
  );
}

const Input = styled.input``;
const Paragraph = styled.p`
  text-align: center;
`;

const Down = styled.a`
  display: none;
`;

const Button = styled.div`
  padding: 10px;
  background: rgb(90, 200, 90);
  border-radius: 10px;
  cursor: pointer;
  margin: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  color: #fff;
  ${(props) => (props.red ? `background: rgb(200, 90, 90);` : null)}

  ${(props) =>
    props.leave
      ? `position: absolute; 
        right: 10px; 
        width: 30px; height:30px;`
      : null}

  :hover {
    box-shadow: 0 0 1px black;
  }
`;

export default ModalTemplate;
