import React, { useContext } from "react";

import {
  Notes,
  Sell,
  Buy,
  Clients,
  Product,
  Main,
  Worker,
  Users,
  Companies,
} from "./Content";
import { useParams } from "react-router-dom";
import User from "../../../Authentication";

const Index = () => {
  const { id } = useParams();
  const { auth } = useContext(User);

  switch (id) {
    case "product":
      return <Product></Product>;
    case "fornecedor":
      return <Clients type="Fornecedor"></Clients>;
    case "comprador":
      return <Clients type="Comprador"></Clients>;
    case "buy":
      return <Buy></Buy>;
    case "sell":
      return <Sell></Sell>;
    case "notes":
      return <Notes></Notes>;
    case "funcionarios":
      return <Worker></Worker>;
    case "usuarios":
      if (auth.user.role === "admin") return <Users></Users>;
      return <Main></Main>;
    case "empresas":
      if (auth.user.role === "admin") return <Companies></Companies>;
      return <Main></Main>;

    default:
      return <Main></Main>;
  }
};

export default Index;
